import React, { useState, useEffect } from 'react';
import type { UserProfile as UserProfileType, CalculatedMetrics } from '../types';
import { calculateMetrics } from '../utils/calculations';
import { saveUserProfile } from '../utils/storage';
import { User, Activity } from 'lucide-react';

interface UserProfileProps {
  profile: UserProfileType | null;
  onProfileUpdate: (profile: UserProfileType) => void;
}

const UserProfile: React.FC<UserProfileProps> = ({ profile, onProfileUpdate }) => {
  const [formData, setFormData] = useState<UserProfileType>({
    gender: 'male',
    age: 25,
    height: 170,
    weight: 70,
    activityLevel: 1.375,
    calorieDeficit: 500
  });

  const [metrics, setMetrics] = useState<CalculatedMetrics | null>(null);
  const [isEditing, setIsEditing] = useState(!profile);

  useEffect(() => {
    if (profile) {
      setFormData(profile);
      const calculatedMetrics = calculateMetrics(profile);
      setMetrics(calculatedMetrics);
    }
  }, [profile]);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    const newValue = name === 'age' || name === 'height' || name === 'weight' || name === 'calorieDeficit' 
      ? Number(value) 
      : name === 'activityLevel' ? Number(value) : value;
    
    setFormData(prev => ({ ...prev, [name]: newValue }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    const calculatedMetrics = calculateMetrics(formData);
    setMetrics(calculatedMetrics);
    await saveUserProfile(formData);
    onProfileUpdate(formData);
    setIsEditing(false);
  };

  const activityLevels = [
    { value: 1.2, label: ' (1.2)', description: '' },
    { value: 1.375, label: ' (1.375)', description: '1-3' },
    { value: 1.55, label: ' (1.55)', description: '3-5' },
    { value: 1.725, label: ' (1.725)', description: '6-7' },
    { value: 1.9, label: ' (1.9)', description: '' }
  ];

  if (!isEditing && profile && metrics) {
    return (
      <div className="bg-white rounded-lg shadow-md p-6">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-xl font-semibold text-gray-800 flex items-center">
            <User className="mr-2" size={24} />
            
          </h2>
          <button
            onClick={() => setIsEditing(true)}
            className="px-4 py-2 bg-blue-500 text-white rounded-md hover:bg-blue-600 transition-colors"
          >
            
          </button>
        </div>
        
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div className="space-y-2">
            <p><span className="font-medium">:</span> {profile.gender === 'male' ? '' : ''}</p>
            <p><span className="font-medium">:</span> {profile.age} </p>
            <p><span className="font-medium">:</span> {profile.height} cm</p>
            <p><span className="font-medium">:</span> {profile.weight} kg</p>
          </div>
          
          <div className="space-y-2">
            <p><span className="font-medium">BMI:</span> {metrics.bmi.toFixed(1)} ({metrics.bmiCategory})</p>
            <p><span className="font-medium">BMR:</span> {metrics.bmr.toFixed(0)} kcal/</p>
            <p><span className="font-medium">TDEE:</span> {metrics.tdee.toFixed(0)} kcal/</p>
            <p><span className="font-medium">:</span> {metrics.dailyCalorieLimit.toFixed(0)} kcal/</p>
          </div>
        </div>
      </div>
    );
  }

  return (
    <div className="bg-white rounded-lg shadow-md p-6">
      <h2 className="text-xl font-semibold text-gray-800 mb-4 flex items-center">
        <User className="mr-2" size={24} />
        {profile ? '' : ''}
      </h2>
      
      <form onSubmit={handleSubmit} className="space-y-4">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1"></label>
            <select
              name="gender"
              value={formData.gender}
              onChange={handleInputChange}
              className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              required
            >
              <option value="male"></option>
              <option value="female"></option>
            </select>
          </div>
          
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1"> ()</label>
            <input
              type="number"
              name="age"
              value={formData.age}
              onChange={handleInputChange}
              min="1"
              max="120"
              className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              required
            />
          </div>
          
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1"> (cm)</label>
            <input
              type="number"
              name="height"
              value={formData.height}
              onChange={handleInputChange}
              min="100"
              max="250"
              className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              required
            />
          </div>
          
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1"> (kg)</label>
            <input
              type="number"
              name="weight"
              value={formData.weight}
              onChange={handleInputChange}
              min="30"
              max="200"
              step="0.1"
              className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              required
            />
          </div>
        </div>
        
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-1 flex items-center">
            <Activity className="mr-1" size={16} />
            
          </label>
          <select
            name="activityLevel"
            value={formData.activityLevel}
            onChange={handleInputChange}
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
            required
          >
            {activityLevels.map(level => (
              <option key={level.value} value={level.value}>
                {level.label} - {level.description}
              </option>
            ))}
          </select>
        </div>
        
        <div>
          <label className="block text-sm font-medium text-gray-700 mb-1"> (kcal)</label>
          <input
            type="number"
            name="calorieDeficit"
            value={formData.calorieDeficit}
            onChange={handleInputChange}
            min="0"
            max="1000"
            step="50"
            className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
            required
          />
          <p className="text-sm text-gray-500 mt-1">500kcal0.5kg/</p>
        </div>
        
        <button
          type="submit"
          className="w-full px-4 py-2 bg-blue-500 text-white rounded-md hover:bg-blue-600 transition-colors"
        >
          {profile ? '' : ''}
        </button>
      </form>
    </div>
  );
};

export default UserProfile;
